import Dexie from "dexie";

let MyPlugin = {};

MyPlugin.install = function (Vue, options) {
    const db = new Dexie(options.dbname);
    db.version(1).stores({
        projects: '++id, name, &slug, description, *tags, created, updated',
        features: '++id, pid, title, options, description, *scenarios, created, updated'
    });
    db.version(2).stores({
        projects: '++id, &slug, *tags, created, updated',
        features: '++id, pid, created, updated',
        vocabularies: '++id, &pid'
    });
    db.on('populate', function () {
        db.projects.add({id:1, name:'SML Toolkit', slug:'sml-toolkit', description: 'It is a tool for project management, from gathering requirements to UML-like design charts, also following BDD-style and thinkings in simple modeling language.', tags: ['bdd-style', 'gathering requirements', 'project management', 'uml-like design charts'], created: 1571318371433, updated: 1571318371433});
        db.features.add({id:1, pid:1, title:'Project management', options:{hasInOrderTo: true, hasIShouldBe: true, hasSoThat: true}, description:{txtInOrderTo: 'do project management', txtIShouldBe: 'a team leader', txtSoThat: 'I can add, modify, delete and list the projects.'}, scenarios:[{title: 'View Projects', isDone: true, options: {hasGiven: true, hasWhen: true, hasThen: true}, given: {given: 'there exists a project named \'Small Toolkit\'', ands: []}, when: {when: 'I visit the page \'/\'', ands: []}, then: {then: 'I see \'Small Toolkit\' listed in \'Projects\' section.', ands: []}}, {title: 'Choose Project', isDone: false, options: {hasGiven: true, hasWhen: true, hasThen: true}, given: {given: 'there exists a project named \'Small Toolkit\'', ands: []}, when: {when: 'I select the \'Small Toolkit\' on the \'Projects\' list', ands: []}, then: {then: 'there will be \'Small Toolkit\' appear in the form \'title\' field', ands: ['also have \'tool\' in \'Auto Suggested Tags\' section', 'also have \'project management\' in \'Saved Tags\' section']}}], created:1571375271588, updated:1571375271588});
        // db.vocabularies.add({id:1, pid:1, nouns:[], verbs:[]});
    });
    db.open();
    Vue.prototype.$db = db;
};

export default MyPlugin;

